<template>
    <div>
       <div class="shopcart">
           <div class="content" @click="toggleList($event)">
               <div class="content-left">
                   <div class="logo-wrapper">
                       <div class="logo" :class="{'highlight':totalCount>0}">
                          <i class="iconfont icon-gouwuche" :class="{'highlight':totalCount>0}"></i>
                       </div>
                       <div class="num" v-show="totalCount>0">{{totalCount}}</div>
                   </div>
                   <div class="price" :class="{'highlight':totalPrice>0}">￥{{totalPrice}}</div>
                   <div class="desc">另外配送费￥{{deliveryPrice}}元</div>
               </div>
               <div class="content-right" @click.stop.pevent="pay">
                   <div class="pay" :class="payClass">
                       {{payDess}}
                   </div>
               </div>
           </div>
          <transition name="fade">
           <div class="shopcart-list" v-show="listShow">
             <div class="list-header">
               <h1 class="title">购物车</h1>
               <span class="empty" @click="empty">清空</span>
             </div>
             <div class="list-content" ref="listContent">
               <ul>
                 <li class="shopcart-food" v-for="food in selectFoods">
                   <span class="name">{{food.name}}</span>
                   <div class="price"><span>￥{{food.price*food.count}}</span></div>
                   <div class="cartControl-wrapper">
                     <cartControl :food="food"></cartControl>
                   </div>
                 </li>
               </ul>
             </div>
           </div>
          </transition>
       </div>
      <transition name="fade">
       <div class="list-mask" v-show="listShow" @click="hideList"></div>
      </transition>
    </div>
</template>

<script type="text/ecmascript-6">
  import cartControl from '../cartControl/cartControl.vue';
  import BScroll from 'better-scroll';
    export default {
      props:{
        selectFoods:{
          type:Array,
          default() {
            return [{price:20,count:2}]
          }
        },
        deliveryPrice:{
          type:Number,
          default:0
        },
        minPrice: {
          type:Number,
          default:0
        }
      },
      data() {
        return {
          fold:true
        }
      },
      computed: {
        payClass() {
          if(this.totalPrice<this.minPrice){
            return 'not=-enough';
          }else{
            return 'enough';
          }
        },
        payDess() {
          if(this.totalPrice === 0){
            return `￥${this.minPrice}元起送`;
          }else if( this.totalPrice < this.minPrice){
            let diff = this.minPrice - this.totalPrice;
            return `还差￥${diff}元起送`;
          }else{
              return `去结算`;
          }
        },
        totalPrice() {
          let total = 0;
          this.selectFoods.forEach( (food) => {
              total += food.price*food.count;
          })
          return total;
        },
        totalCount() {
          let count =0;
          this.selectFoods.forEach( (food) => {
            count += food.count;
          })
          return count;
        },
        listShow() {
          if(!this.totalCount) {
            this.fold = true;
            return false;
          }
          let show = !this.fold;
          if ( show) {
             this.$nextTick( () => {
                 if (!this.scroll) {
                   this.scroll = new BScroll(this.$refs.listContent,{
                     click:true
                   });
                 }else {
                   this.scroll.refresh();
                 }
             });
          }
          return show;
        }
      },
      methods:{
        empty() {
          this.selectFoods.forEach( (food) => {
            food.count = 0;
          })
        },
        hideList() {
          this.fold = true;
          console.log(123);
        },
        toggleList() {
          if (!this.totalCount) {
            return;
          }
          this.fold = !this.fold;
          console.log(1212);
          console.log(this.fold);
        },
        pay() {
          if (this.totalPrice < this.minPrice) {
            return ;
          }
          window.alert('支付'+this.totalPrice+'元')
        }
      },
      mounted() {
        console.log(121);
        console.log(this.selectFoods);
      },
      components:{
        cartControl
      }
    }
</script>

<style lang="stylus" rel="stylesheet/stylus">
@import "shopCart.styl";
@import "../../common/stylus/animate.css";
</style>


